import Question from "./Question/Question";
import React from "react";
import styles from "./Questions.module.css";

const questions = prop => {
  console.log(prop.questions);
  const questions = prop.questions
    ? prop.questions.map((item, index) => {
        return (
          <Question
            key={item._id}
            questionId={item.id}
            questionIndex={index + 1}
            question={item.question}
            date={item.createdAt}
            clicked={() => prop.clicked(item)}
          />
        );
      })
    : null;
  return <div className={styles.Questions}>{questions}</div>;
};

export default questions;
