import React from "react";
import styles from "./Question.module.css";

import TimeAgo from "react-timeago";
import enString from "react-timeago/lib/language-strings/en";
import buildFormatter from "react-timeago/lib/formatters/buildFormatter";
const question = props => {
  const formatter = buildFormatter(enString);

  return (
    <div className={styles.Question} onClick={props.clicked}>
      <h4>Question {props.questionIndex}</h4>
      <p>{props.question}</p>
      <small>
        <TimeAgo date={props.date} formatter={formatter} />
      </small>
    </div>
  );
};
export default question;
