import React, { Component } from "react";
import { connect } from "react-redux";

import Questions from "../../components/Questions/Questions";
import Aux from "../../hoc/Aux/Aux";
import * as actions from "../../store/actions/index";
import Footer from "../../components/Questions/Footer/Footer";
import { socket } from "../../App";

class QuestionsCtr extends Component {
  componentDidMount() {
    console.log(this.props);
    this.props.onInitQuestions();

    socket.on("addquestion", data => {
      console.log("test", data.question);
      this.props.onGotQuestions(data.question);
    });
  }

  clickedQuesitoning = question => {
    this.props.history.push({
      pathname: "/answers",
      search: "?" + "question=" + question._id
    });
  };
  componentWillUnmount() {
    socket.off("addquestion");
  }
  render() {
    console.log(this.props.questions);

    return (
      <Aux>
        <Questions
          questions={this.props.questions}
          clicked={this.clickedQuesitoning}
        />
      </Aux>
    );
  }
}

const mapStateToProps = state => {
  return {
    questions: state.question.questions
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onInitQuestions: () => dispatch(actions.initQuestions()),
    onGotQuestions: question => dispatch(actions.addOneQuestion(question))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(QuestionsCtr);
