import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import { connect } from "react-redux";
import logo from "./logo.svg";
import * as actions from "./store/actions/index";
import styles from "./App.module.css";
import axios from "./axios";
import Modal from "./components/UI/Modal/Modal";
import Layout from "./hoc/Layout/Layout";
import Loader from "./components/UI/Loader/Loader";
import QuestionsCtr from "./containers/QuestionsCtr/QuestionsCtr";
import AddQuestionCtr from "./containers/AddQuestionCtr/AddQuestionCtr";
import AnswerCtr from "./containers/AnswerCtr/AnswerCtr";
import { spring, AnimatedSwitch } from "react-router-transition";
import { withRouter } from "react-router-dom";
import openSocket from "socket.io-client";
var socket;
class App extends Component {
  constructor(props) {
    super(props);
    socket = openSocket("http://app.tedxchulalongkornu.net/");
  }
  state = {
    height: window.innerHeight + 120
  };

  componentDidMount() {
    console.log(this.props.location);
    window.onhashchange = () => {
      console.log("change");
      this.props.history.replace(
        "." + this.props.location.pathname + this.props.location.search
      );
      this.props.clickCancel();
    };
    window.addEventListener("hashchange", window.onhashchange, false);
    this.setState({ height: window.innerHeight + 120 });
  }

  updateDimensions() {
    console.log(this.state.height);
  }

  submitHandler = () => {
    var search = this.props.search ? this.props.search : "";
    this.props.history.push(
      "." + this.props.location.pathname + search + "#confirm"
    );
    this.props.clickConfirm();
  };
  questionCancelHandler = () => {
    this.props.history.goBack();
    this.props.clickCancel();
  };

  confirmHandler = () => {
    if (this.props.location.pathname == "/addquestion") {
      this.props.history.goBack();
      this.props.clickCancel();
      this.props.startLoading();
      axios
        .post("/api/question", { question: this.props.val })
        .then(response => {
          this.props.finish();
          console.log("clicked");

          this.props.history.goBack();
        })
        .catch(error => {
          this.props.finish();
          alert("Please Check Your Internet Connection");
        });
    } else if (this.props.location.pathname == "/answers") {
      const question_id = this.props.location.search.split("=")[1].trim();
      this.props.clickCancel();
      this.props.startLoading();
      axios
        .post("/api/answer", {
          answer: this.props.ans,
          question_id: question_id
        })
        .then(results => {
          console.log(results);

          this.props.finish();
          window.location.reload();
        })
        .catch(err => {
          this.props.finish();
          console.log(err);
        });
    }
  };

  render() {
    function mapStyles(styles) {
      return {
        opacity: styles.opacity,
        transform: `scale(${styles.scale})`
      };
    }

    // wrap the `spring` helper to use a bouncy config
    function bounce(val) {
      return spring(val, {
        stiffness: 330,
        damping: 22
      });
    }

    // child matches will...
    const bounceTransition = {
      // start in a transparent, upscaled state
      atEnter: {
        opacity: 0,
        scale: 1.2
      },
      // leave in a transparent, downscaled state
      atLeave: {
        opacity: bounce(0),
        scale: bounce(0.8)
      },
      // and rest at an opaque, normally-scaled state
      atActive: {
        opacity: bounce(1),
        scale: bounce(1)
      }
    };

    return (
      <div>
        <Layout>
          <AnimatedSwitch
            atEnter={bounceTransition.atEnter}
            atLeave={bounceTransition.atLeave}
            atActive={bounceTransition.atActive}
            mapStyles={mapStyles}
            className={styles.routewrapper}
          >
            <Route
              path="/addquestion"
              render={() => <AddQuestionCtr clicked={this.submitHandler} />}
            />
            <Route path="/answers" component={AnswerCtr} />
            <Route path="/" component={QuestionsCtr} />
          </AnimatedSwitch>
        </Layout>
        <Modal
          show={this.props.confirm}
          modalClosed={this.questionCancelHandler}
        >
          <p className={styles.ModalDetail}>
            Please confirm your question .You can't edit when you submited
          </p>
          <div
            className={styles.ModalBtn}
            style={{ display: "flex", float: "right" }}
          >
            <p onClick={this.questionCancelHandler}>Cancel</p>
            <p
              style={{ marginLeft: 20, alignSelf: "center" }}
              onClick={this.confirmHandler}
            >
              Confirm
            </p>
          </div>
        </Modal>
        {this.props.loading ? <Loader /> : null}

        <div className={styles.area} style={{ height: this.state.height }}>
          <ul className={styles.circles}>
            <li />
            <li />
            <li />
            <li />
            <li />
            <li />
            <li />
            <li />
            <li />
            <li />
          </ul>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    val: state.question.questionsval,
    confirm: state.answer.confirm,
    ans: state.answer.answerval,
    loading: state.question.loading
  };
};
const mapDispatchToPrps = dispatch => {
  return {
    clickConfirm: () => dispatch(actions.confirm()),
    clickCancel: () => dispatch(actions.cancel()),
    setConfirm: () => dispatch(actions.setConfirm()),
    finish: () => dispatch(actions.finishLoading()),
    startLoading: () => dispatch(actions.startRequest())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToPrps
)(withRouter(App));
export { socket };
