import React, { Component } from "react";
import { connect } from "react-redux";
import Aux from "../Aux/Aux";
import * as actions from "../../store/actions/index";
import Toolbar from "../../components/UI/Toolbar/Toolbar";
import SideDrawer from "../../components/UI/SideDrawer/SideDrawer";
import Footer from "../../components/Questions/Footer/Footer";
import FooterAnswer from "../../components/Questions/FooterAnswer/FooterAnswer";
import styles from "./Layout.module.css";
import { socket } from "../../App";
import { withRouter } from "react-router-dom";
class Layout extends Component {
  state = {
    numClient: 0,
    online: false
  };
  componentDidMount() {
    console.log("Iam mounted");
    var socketio = socket.connect();

    socket.on("connect", () => {
      console.log("check", socketio.connected);
      this.setState({ online: true });
    });
    socket.on("disconnect", () => {
      console.log("check", socketio.connected);
      this.setState({ online: false });
    });
    socket.on("numclient", data => {
      console.log(data);
      this.setState({ numClient: data });
    });
  }
  clickHandler = () => {
    var search = this.props.location.search ? this.props.location.search : "";
    this.props.history.push(
      "." + this.props.location.pathname + search + "#confirm"
    );
    this.props.clickConfirm();
  };
  render() {
    let footer = null;
    if (this.props.location.pathname === "/") {
      footer = <Footer />;
    } else if (this.props.location.pathname === "/answers") {
      footer = <FooterAnswer clickSubmit={this.clickHandler} />;
    }
    return (
      <Aux>
        <Toolbar numClient={this.state.numClient} online={this.state.online} />
        <SideDrawer />
        <main className={styles.Content}>{this.props.children}</main>
        {footer}
      </Aux>
    );
  }
}

const mapStateToProps = state => {
  return {
    confirm: state.answer.confirm
  };
};
const mapDispatchToPrps = dispatch => {
  return {
    clickConfirm: () => dispatch(actions.confirm()),
    clickCancel: () => dispatch(actions.cancel())
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToPrps
)(withRouter(Layout));
