import React, { Component } from "react";
import { connect } from "react-redux";

import AddQuestionWrapper from "../../components/AddQuestionWrapper/AddQuestionWrapper";
import Aux from "../../hoc/Aux/Aux";
import * as actions from "../../store/actions/index";

class AddQuestionCtr extends Component {
  changeHandler = event => {
    this.props.onChangeQuestionValue(event.target.value);
  };
  render() {
    return (
      <Aux>
        <AddQuestionWrapper
          clicked={this.props.clicked}
          content={this.props.val}
          change={this.changeHandler}
        />
      </Aux>
    );
  }
}
const mapStateToProps = state => {
  return {
    val: state.questions
  };
};
const mapDispatchToProps = dispatch => {
  return {
    onChangeQuestionValue: val => dispatch(actions.changeQuestions(val))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AddQuestionCtr);
