import * as actionTypes from "../actions/actionTypes";

const initialState = {
  questions: null,
  questionsval: "",
  cur_question: null,
  loading: false
};
const setQuestions = (state, action) => {
  return {
    ...state,
    questions: action.questions,
    loading: false
  };
};

const changeQuestions = (state, action) => {
  return {
    ...state,
    questionsval: action.value
  };
};
const addOneQuestion = (state, action) => {
  return {
    ...state,
    questions: [...state.questions, action.question]
  };
};

const loadingFinish = (state, action) => {
  return {
    ...state,
    loading: false
  };
};

const startFinish = (state, action) => {
  return {
    ...state,
    loading: true
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_QUESTION:
      return setQuestions(state, action);
    case actionTypes.CHANGE_QUESTION:
      return changeQuestions(state, action);
    case actionTypes.ADD_ONEQUESTION:
      return addOneQuestion(state, action);
    case actionTypes.START_REQUEST:
      return startFinish(state, action);
    case actionTypes.FINISH_LOADING:
      return loadingFinish(state, action);
  }
  return state;
};

export default reducer;
