import React, { Component } from "react";
import styles from "./FooterAnswer.module.css";
import { connect } from "react-redux";
import * as actions from "../../../store/actions/index";
import FooterAnswerBtnQ from "./FooterAnswerBtnQ/FooterAnswerBtnQ";
import { withRouter } from "react-router-dom";
import axios from "../../../axios";
class FooterAnswer extends Component {
  state = {
    answering: false,
    exited: false,
    classes: [styles.Footer]
  };
  exitedHandler = () => {
    console.log("Exited");

    this.setState({ exited: true });
  };
  changeHandler = event => {
    this.props.onChange(event.target.value);
  };
  clickSubmitHandler = () => {
    const question_id = this.props.location.search.split("=")[1].trim();
  };
  clickedQuesitoning = () => {
    this.setState(prevState => ({
      answering: !prevState.answering
    }));
  };
  componentDidMount() {
    setTimeout(() => {
      this.setState({ classes: [...this.state.classes, styles.FooterIn] });
    }, 300);
  }
  render() {
    return (
      <div className={this.state.classes.join(" ")}>
        <FooterAnswerBtnQ
          change={this.changeHandler}
          onExited={this.exitedHandler}
          exited={this.state.exited}
          answering={!this.state.answering}
          clicked={this.clickedQuesitoning}
          click={this.props.clickSubmit}
        />
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    ans: state.answer.answerval
  };
};
const mapDispatchToProps = dispatch => {
  return {
    onChange: ans => dispatch(actions.changeAnswer(ans))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(FooterAnswer));
