import * as actionTypes from "./actionTypes";
import axios from "../../axios";

export const setQuestions = questions => {
  return {
    type: actionTypes.SET_QUESTION,
    questions: questions
  };
};

export const fetchQuestionsFailed = () => {
  return {
    type: actionTypes.FETCH_QUESTIONS_FAILED
  };
};
export const changeQuestions = value => {
  return {
    type: actionTypes.CHANGE_QUESTION,
    value
  };
};

export const addOneQuestion = question => {
  return {
    type: actionTypes.ADD_ONEQUESTION,
    question
  };
};
export const startRequest = () => {
  return {
    type: actionTypes.START_REQUEST
  };
};

export const setConfirm = () => {
  return { type: actionTypes.SET_CONFIRM };
};

export const finishLoading = () => {
  return { type: actionTypes.FINISH_LOADING };
};

export const initQuestions = () => {
  return dispatch => {
    dispatch(startRequest());
    axios
      .get("api/questions")
      .then(response => {
        console.log(response.data.questions);

        dispatch(setQuestions(response.data.questions));
      })
      .catch(error => {
        dispatch(finishLoading());
        alert("Please Check Your Internet Connection");
      });
  };
};
