import React from "react";
import Answer from "./Answer/Answer";

const answers = props => {
  return (
    <div style={{ marginBottom: 70 }}>
      {props.answers.map((item, index) => {
        return (
          <Answer
            key={item._id}
            content={item.answer}
            date={item.createdAt}
            index={index + 1}
          />
        );
      })}
    </div>
  );
};
export default answers;
