import React from "react";
import styles from "./AddQuestionWrapper.module.css";

const addquestionWrapper = props => (
  <div className={styles.AddQuestionWrapper}>
    <h3>Questioning the truth</h3>
    <input
      type="text"
      value={props.content}
      onChange={props.change}
      placeholder="Enter Your Question"
    />
    <button onClick={props.clicked}>Submit</button>
  </div>
);

export default addquestionWrapper;
