import React, { Component } from "react";
import Aux from "../../hoc/Aux/Aux";
import styles from "./AnswerCtr.module.css";
import * as actions from "../../store/actions/index";
import { connect } from "react-redux";
import Answers from "../../components/Answers/Answers";
import axios from "../../axios";
import { socket } from "../../App";
class AnswerCtr extends Component {
  state = {
    answers: null,
    question: null
  };
  componentDidMount() {
    const question_id = this.props.location.search.split("=")[1].trim();
    this.props.startLoading();
    socket.on(question_id, data => {
      this.setState({ answers: [...this.state.answers, data.answer] });
    });
    axios
      .post("/api/answers", { question_id: question_id })
      .then(response => {
        console.log(response.data.answer);
        this.props.finish();
        this.setState({ answers: response.data.answer });
      })
      .catch(error => {
        this.props.finish();
      });

    axios
      .post("/api/onequestion", { question_id: question_id })
      .then(response => {
        console.log(response.data.questions.question);
        this.props.finish();
        this.setState({ question: response.data.questions.question });
      })
      .catch(error => {
        this.props.finish();
      });
  }

  componentWillUnmount() {
    const question_id = this.props.location.search.split("=")[1].trim();
    socket.off(question_id);
  }
  render() {
    return (
      <Aux>
        {this.state.question ? (
          <div className={styles.Question}>
            <h3>Question</h3>
            <p>{this.state.question}</p>
          </div>
        ) : null}

        {this.state.answers ? <Answers answers={this.state.answers} /> : null}
      </Aux>
    );
  }
}
const mapStateToProb = state => {
  return {
    loading: state.question.loading
  };
};

const mapDispatchToPrps = dispatch => {
  return {
    finish: () => dispatch(actions.finishLoading()),
    startLoading: () => dispatch(actions.startRequest())
  };
};

export default connect(
  mapStateToProb,
  mapDispatchToPrps
)(AnswerCtr);
